<template>
  <div class="user-collect">
    <el-card class="collects" shadow="never">
      <div slot="header" class="not-importent collects-header">
        <div>
          <span>我的收藏<i class="el-icon-loading" v-show="loading"></i></span>
        </div>
      </div>
      <div
        v-for="(collect, index) in collects"
        :key="collect.time"
        class="collect"
      >
        <el-divider v-if="index"></el-divider>
        <div class="flex justify-between">
          <div class="mr-4">
            <el-link
              @click="
                $router.push({ path: '/document', query: { url: collect.url } })
              "
              >{{ collect.title }}</el-link
            >
          </div>
          <el-popconfirm title="确定删除吗？" @confirm="removeCollect(collect)">
            <el-button
              slot="reference"
              class="collect-delete"
              type="text"
              icon="el-icon-delete"
            >
              删除
            </el-button>
          </el-popconfirm>
        </div>
      </div>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      collects: [],
      loading: false,
    }
  },
  async created() {
    this.loading = true
    try {
      const data = await fetch(
        "https://service-o4amteg7-1252108641.sh.apigw.tencentcs.com/release/tcb-dba",
        {
          method: "POST",
          body: JSON.stringify({
            action: "ty-query-reader-user-collect",
            data: {
              openid: this.cloudAuth.currentUser.uid,
            },
          }),
        }
      ).then((res) => res.json())
      this.collects = data
    } catch (error) {
      console.log(error)
    } finally {
      this.loading = false
    }
  },
  methods: {
    async removeCollect(collect) {
      const data = await fetch(
        "https://service-o4amteg7-1252108641.sh.apigw.tencentcs.com/release/tcb-dba",
        {
          method: "POST",
          body: JSON.stringify({
            action: "ty-remove-reader-user-collect",
            data: {
              openid: this.cloudAuth.currentUser.uid,
              collectId: collect._id,
            },
          }),
        }
      ).then((res) => res.json())
      console.log(data)
      if (data.code) {
        this.$message.error("操作失败，请稍后重试")
      } else {
        // 从本地中清除
        this.collects = this.collects.filter(
          (collect) => collect._id != collect._id
        )
      }
    },
  },
}
</script>
