<template>
  <div class="inedx">
    <div class="flex justify-between items-center">
      <div class="logo">
        <img src="../assets/logo.png" alt="logo">
      </div>
      <div>
        <el-button
          type="text"
          @click="$router.push({ path: '/cloud' })"
          size="small"
          icon="el-icon-user"
          >账户</el-button
        >
      </div>
    </div>
    <!-- <div class="search">
      <div class="search-inp">
        <el-input
          placeholder="搜索或输入网址"
          v-model="url"
          clearable
          prefix-icon="el-icon-search"
        />
      </div>
      <div class="search-btn">
        <el-dropdown
          split-button
          type="primary"
          trigger="click"
          @click="search(url, 'google')"
          @command="clickMore"
        >
          搜索
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="douban">搜日记</el-dropdown-item>
            <el-dropdown-item command="v2ex">搜热议</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div> -->
    <div class="cursor-pointer mb-8 flex justify-center items-center bg-gray-100 hover:bg-gray-200 p-8" @click="$router.push({ path: 'document',  query: { url: 'https://v2ex.com' } })">
      <div class="mr-2 bold">open</div>
      <img src="../assets/v2ex-logo.png" alt="logo" class="w-40">
    </div>
    <el-card class="historys" shadow="never" v-if="historys.length">
      <div slot="header" class="not-importent historys-header">
        <div>
          <span>历史记录</span>
        </div>
        <div>
          <el-button
            @click="clearHistorys"
            type="text"
            class="el-icon-delete"
          ></el-button>
        </div>
      </div>
      <div
        v-for="(history, index) in historys"
        :key="history.time"
        class="history"
      >
        <el-divider v-if="index"></el-divider>
        <div class="history-inner">
          <div>
            <el-link @click="openHistoryDocument(history)">{{
              history.title
            }}</el-link>
          </div>
          <!-- <div class="history-star" @click="switchHistoryStar(history)">
            <i :class="'el-icon-star-' + (history.star ? 'on' : 'off')"></i>
          </div> -->
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "Index",
  data() {
    return {
      url: "",
      historys: [], // 历史记录
    }
  },
  activated() {
    // 历史记录
    const historys = JSON.parse(localStorage.getItem("documentHistory") || "[]")
    this.historys = historys.slice(0, 100).map((item) => {
      item.star = !!item.star
      return item
    })
  },
  computed: {
    allHistory() {
      return this.staredHistorys.concat(this.historys)
    },
  },
  methods: {
    search(url, source) {
      if (url) {
        this.$router.push({ path: "/document", query: { url, source } })
      }
    },
    openHistoryDocument(history) {
      // 打开文档
      this.$router.push({ path: "/document", query: { url: history.url } })
    },
    switchHistoryStar(history) {
      history.star = !history.star
      history.time = Date.now()

      localStorage.setItem("documentHistory", JSON.stringify(this.historys))
    },
    clearHistorys() {
      this.historys = this.historys.filter((item) => {
        return item.star
      })
      localStorage.setItem("documentHistory", JSON.stringify(this.historys))
    },
    clickMore(more) {
      this.search(this.url, more)
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
