  display: flex;
}
.search-inp {
  flex: auto;
}
.search-btn {
  flex: none;
  margin-left: 10px;
}
.history {
  margin: 10px 0;
}
.historys-header {
  align-items: center;
  display: flex;
  justify-content: space-between;
}
.history-inner {
  align-items: center;
  display: flex;
  justify-content: space-between;
}
.history-star {
  margin-left: 10px;
}
</style>
