<template>
  <div class="document" v-if="document">
    <el-page-header
      @back="$router.push('/')"
      :content="document.title"
      title="首页"
    >
    </el-page-header>
    <div :class="showShareCard ? 'opacity-10' : 0">
      <div
        class="content"
        v-for="content in document.list"
        :key="content.insideId"
      >
        <p v-if="content.type == 'text'">
          <el-link
            type="primary"
            v-if="content.ref"
            @click="$router.push({ query: { url: content.ref } })"
            >{{ content.content }}</el-link
          >
          <span v-else>{{ content.content }}</span>
        </p>
        <p v-else-if="content.type == 'img'">
          <el-image
            :src="content.content"
            class="w-32 h-32"
            :preview-src-list="imgSrcList"
          />
        </p>
        <p v-else-if="content.type == 'video'">
          <video :src="content.content" controls></video>
        </p>
        <p v-else>
          <span>[未知类型]{{ content.content }}</span>
        </p>
      </div>
    </div>
    <div class="fixed bottom-1 right-1 z-10">
      <div
        ref="shareCardDemo"
        class="p-4 border-gray-300 w-80 hidden"
        :style="{ backgroundColor: shareCard.bgColor }"
      >
        <h2 class="text-lg text-center text-black font-bold mb-2">{{ document.title }}</h2>
        <div v-for="img in shareCard.imgs"
            :key="img" class="mt-4 max-w-80">
          <el-image
            @click="useImgInShardCard(img)"
            :src="img"
            class="w-full"
          />
        </div>
        <p
          class="py-1 break-all"
          v-for="item in shareCardSelectedItems"
          :key="item.insideId"
        >
          {{ item.content }}
        </p>
        <p class="py-1 break-all">...</p>
        <div class="flex items-center mt-2 justify-between">
          <div class="text-xs">识别二维码浏览全文</div>
          <div>
            <img src="" alt="" ref="qrcode" class="block w-16 h-16" />
          </div>
        </div>
      </div>
      <div ref="shareCard" v-show="showShareCard" class="p-2">
        <img src="" alt="" ref="shareCardImg" class="w-80" />
        <div class="text-xs text-gray-500 text-center bg-white pt-2 w-80">
          <div>
            ps:长按此卡片可保存或分享
          </div>
          <div class="ctrl-card mt-2">
            <div class="w-80 overflow-auto flex gap-2" v-if="shareCardImgs.length">
              <div v-for="img in shareCardImgs"
                  :key="img">
                <el-image
                  @click="useImgInShardCard(img)"
                  :src="img"
                  fit="cover"
                  class="w-16 h-16 cursor-pointer"
                />
              </div>
            </div>
            <div
              class="flex items-center justify-center mt-2 w-80"
              
            >
              <div class="mr-2 flex">
                <el-color-picker
                  v-model="shareCard.bgColor"
                  show-alpha
                  :predefine="shareCard.predefineColors"
                >
                </el-color-picker>
              </div>
              <el-select
                v-model="shareCard.checkList"
                multiple
                popper-class="w-80"
                :multiple-limit="3"
                :popper-append-to-body="false"
                placeholder="请选择"
              >
                <el-option
                  v-for="item in shareCardItems"
                  :key="item.insideId"
                  :label="item.content"
                  :value="item.insideId"
                >
                </el-option>
              </el-select>
            </div>
          </div>
          <div class="py-2">
            <el-button
              type="primary"
              size="small"
              @click="downloadShareCard"
              icon="el-icon-download"
              >保存</el-button
            >
            <el-button
              type="default"
              size="small"
              @click="cancleShare"
              icon="el-icon-close"
              >取消</el-button
            >
            <el-button
              type="default"
              size="small"
              @click="shareByCard"
              icon="el-icon-refresh"
              >重绘</el-button
            >
          </div>
        </div>
      </div>
      <div class="p-2" v-show="!showShareCard">
        <el-button
          type="primary"
          size="small"
          @click="shareByCard"
          icon="el-icon-share"
          >分享</el-button
        >
        <el-button
          type="default"
          size="small"
          @click="addUserCollect"
          icon="el-icon-star-off"
          >收藏</el-button
        >
        <el-button
          type="text"
          @click="$router.push({ path: '/cloud' })"
          size="small"
          icon="el-icon-user"
          >账户</el-button
        >
      </div>
    </div>
    <div class="not-importent host-tip">
      <el-link :href="document.url" target="_blank" type="info"
        >内容由{{ urlHost }}提供</el-link
      >
    </div>
  </div>
</template>

<script>
import QRCode from "qrcode"
import html2canvas from "html2canvas"
export default {
  name: "Document",
  props: {
    url: {
      type: String,
      required: true,
    },
    source: {
      type: String,
    },
  },
  data() {
    return {
      document: null,
      cachedDocuments: {},
      showShareCard: false,
      shareCard: {
        bgColor: "rgba(255, 69, 0, 0.68)",
        checkList: [],
        imgs: [], // 内嵌的图片
        predefineColors: [
          "#ff4500",
          "#ff8c00",
          "#ffd700",
          "#90ee90",
          "#00ced1",
          "#1e90ff",
          "#c71585",
          "rgba(255, 69, 0, 0.68)",
          "rgb(255, 120, 0)",
          "hsv(51, 100, 98)",
          "hsva(120, 40, 94, 0.5)",
          "hsl(181, 100%, 37%)",
          "hsla(209, 100%, 56%, 0.73)",
          "#c7158577",
        ],
      },
    }
  },
  computed: {
    urlHost: function() {
      if (!this.document) {
        return ""
      }
      var result = /^https?:\/\/(.+?)(?:(?:\/)|$)/i.exec(this.document.url)
      return result[1] || this.document.url
    },
    imgSrcList: function() {
      if (!this.document) {
        return []
      }
      return this.document.list
        .filter((item) => item.type == "img")
        .map((item) => item.content)
    },
    shareCardImgs() {
      return this.imgSrcList.slice(0, 30)
    },
    shareCardItems() {
      if (!this.document) {
        return []
      }
      return this.document.list.slice(0, 20)
    },
    shareCardSelectedItems() {
      if (!this.document) {
        return []
      }
      return this.document.list.filter((item) =>
        this.shareCard.checkList.includes(item.insideId)
      )
    },
  },
  watch: {
    url: function(nVal) {
      if (nVal) {
        // 检查缓存
        const cachedDocument = this.cachedDocuments[nVal]
        if (cachedDocument) {
          this.document = cachedDocument
          // 添加历史记录
          this.pushHistory()
          // 滚动到顶部
          scrollTo(0, 0)
        } else {
          this.loadDocument()
        }
      }
    },
    document(doc) {
      if (doc) {
        window.document.title = doc.title
      }
    },
    "shareCard.bgColor": "shareByCard",
    "shareCard.checkList": {
      deep: true,
      handler: "shareByCard",
    },
    "shareCard.imgs": {
      deep: true,
      handler(){
        const loading = this.$loading()
        setTimeout(()=>{
          loading.close();
          this.shareByCard();
        }, 1000)
      }
    },
  },
  created() {
    this.loadDocument()
  },
  methods: {
    useImgInShardCard(img){
      const index = this.shareCard.imgs.indexOf(img)
      if(index >= 0){
        this.shareCard.imgs.splice(index, 1)
      }else{
        this.shareCard.imgs = [img]
      }
    },
    downloadShareCard() {
      var oA = document.createElement("a")
      oA.download = "share.png"
      oA.href = this.$refs.shareCardImg.src
      document.body.appendChild(oA)
      oA.click()
      oA.remove() // 下载之后把创建的元素删除
      this.cancleShare()
    },
    cancleShare() {
      this.showShareCard = false
    },
    async shareByCard() {
      // 生成二维码
      const url = await QRCode.toDataURL(this.document.url, {
        color: { light: "#ffffff00" },
      })
      console.log(url)
      this.$refs.qrcode.src = url

      // 导出卡片
      this.$refs.shareCardDemo.style.display = "block"
      const canvas = (this.shareCardCanvas = await html2canvas(
        this.$refs.shareCardDemo,
        {
          allowTaint: true,
          useCORS: true,
          imageTimeout: 30000
        }
      ))
      // 插入到文档里面
      this.$refs.shareCardDemo.style.display = "none"
      this.$refs.shareCardImg.src = canvas.toDataURL("image/png")

      this.showShareCard = true
    },
    async loadDocument() {
      const loading = this.$loading()
      console.log(`加载：${this.url}`)
      try {
        const res = await fetch(
          `${process.env.VUE_APP_LOADER_URL}?url=${this.url}&source=${this.source}`
        )
        const json = await res.json()
        console.log(json)
        this.document = this.cachedDocuments[this.url] = json
        // 关闭加载遮罩
        loading.close()
        // 添加历史记录
        this.pushHistory()
        // 滚动到顶部
        scrollTo(0, 0)
      } catch (error) {
        this.$message.error(error.message)
      } finally {
        // 关闭加载遮罩
        loading.close()
      }
    },

    pushHistory() {
      let documentHistory = JSON.parse(
        localStorage.getItem("documentHistory") || "[]"
      )
      // 已经有历史记录不再存储历史记录
      const newHsitory = {
        title: this.document.title,
        url: this.document.url,
        time: Date.now(),
      }
      documentHistory = documentHistory.filter((item) => {
        return item.star || item.url != newHsitory.url
      })
      documentHistory.unshift(newHsitory)
      localStorage.setItem("documentHistory", JSON.stringify(documentHistory))
    },

    async addUserCollect() {
      if (this.cloudAuth.hasLoginState()) {
        const data = await fetch(
          "https://service-o4amteg7-1252108641.sh.apigw.tencentcs.com/release/tcb-dba",
          {
            method: "POST",
            body: JSON.stringify({
              action: "ty-add-reader-user-collect",
              data: {
                openid: this.cloudAuth.currentUser.uid,
                url: this.document.url,
                title: this.document.title,
              },
            }),
          }
        ).then((res) => res.json())
        if (data.res.code) {
          console.log(data.res)
          this.$message.error("操作失败，请稍后重试")
        } else {
          this.$message.success("已收藏")
        }
      } else {
        this.$message.warning("需登录")
      }
    },
  },
}
</script>

<style scoped>
.header {
  margin: 10px 0;
}
.title {
  font-size: 16px;
}
.content {
  word-break: break-all;
  margin: 10px 0;
}
.host-tip {
  text-align: center;
  margin: 30px 0;
}
.document::v-deep .el-page-header__title {
  flex: none;
  align-self: center;
}
</style>
