import Vue from "vue"
import App from "./App.vue"
import cloudbase from "@cloudbase/js-sdk"
import Index from "./components/Index.vue"
import Document from "./components/Document.vue"
import EoCloud from "./components/EoCloud.vue"
import VueRouter from "vue-router"
import ElementUI from "element-ui"
import "tailwindcss/tailwind.css"
import "./assets/element-ui/index.css"
import "./assets/my-style.css"

// 链接云服务
const cloud = (Vue.prototype.cloud = cloudbase.init({
  env: process.env.VUE_APP_CLOUD_NAME,
}))
Vue.prototype.cloudAuth = cloud.auth({
  persistence: "local",
})

// 应用路由插件
Vue.use(VueRouter)
// 应用UI
Vue.use(ElementUI)

Vue.config.productionTip = false

const routes = [
  {
    path: "/",
    component: Index,
  },
  {
    path: "/index",
    component: Index,
  },
  {
    path: "/document",
    component: Document,
    props: (route) => ({
      ...route.query,
    }),
  },
  { path: "/cloud", component: EoCloud, props: true },
]

const router = new VueRouter({
  routes, // （缩写）相当于 routes: routes
})

new Vue({
  render: (h) => h(App),
  router,
}).$mount("#app")
